<template>
  <div>
    <div class="mdui-appbar mdui-appbar-fixed">
      <div class="mdui-toolbar mdui-color-theme">
        <span
          @click="inst.toggle()"
          class="mdui-btn mdui-btn-icon mdui-ripple mdui-ripple-white"
        >
          <i class="mdui-icon material-icons">menu</i>
        </span>
        <a href="/" class="mdui-typo-headline mdui-hidden-xs">{{ title }}</a>
        <span class="mdui-typo-title">{{ subTitle }}</span>
        <div class="mdui-toolbar-spacer"></div>
        <!--
        <div v-if="this">
          <button
            @click="playControl()"
            mdui-tooltip="{content: '停止播放音乐'}"
            class="mdui-btn mdui-btn-icon mdui-ripple mdui-ripple-white"
          >
            <i class="mdui-icon material-icons">play_arrow</i>
          </button>
        </div>
        <div v-else>
          <button
            @click="playControl()"
            mdui-tooltip="{content: '开始播放音乐'}"
            class="mdui-btn mdui-btn-icon mdui-ripple mdui-ripple-white"
          >
            <i class="mdui-icon material-icons">stop</i>
          </button>
        </div>
        -->
      </div>
      <div class="mdui-progress" v-if="isLoading || isLoadingPage">
        <div class="mdui-progress-indeterminate"></div>
      </div>
    </div>

    <div class="mdui-drawer" id="main-drawer">
      <div
        class="mdui-list"
        mdui-collapse="{accordion: true}"
        style="margin-bottom: 76px"
      >
        <div class="mdui-list">
          <a class="mdui-list-item mdui-ripple" @click="navto('/')">
            <i class="mdui-list-item-icon mdui-icon material-icons">home</i>
            <div class="mdui-list-item-content">主页</div></a
          >
          <a class="mdui-list-item mdui-ripple" @click="navto('/submit')">
            <i class="mdui-list-item-icon mdui-icon material-icons">favorite</i>
            <div class="mdui-list-item-content">去表白</div></a
          >
          <a class="mdui-list-item mdui-ripple" @click="navto('/more')">
            <i class="mdui-list-item-icon mdui-icon material-icons"
              >tag_faces</i
            >
            <div class="mdui-list-item-content">更多功能</div></a
          >
          <a class="mdui-list-item mdui-ripple" @click="navto('/about')">
            <i class="mdui-list-item-icon mdui-icon material-icons">code</i>
            <div class="mdui-list-item-content">关于本站</div></a
          >
        </div>
      </div>
      <div class="copyright">
        <div class="mdui-typo">
          <p class="mdui-typo-caption-opacity">© 2021 UnknownO</p>
          <p class="mdui-typo-caption-opacity">
            Powered by <a href="https://mdui.org" target="_blank">MDUI</a> &amp;
            <a href="https://vuejs.cn" target="_blank">VUE</a>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<style>
.copyright {
  box-sizing: border-box;
  width: 100%;
  padding: 10px 16px;
  position: absolute;
  bottom: 0;
}
</style>

<script>
import { request } from "../utils/request.js";
export default {
  data() {
    return {
      isLoadingPage: false,
    };
  },
  mounted: function () {
    document.title = this.subTitle + " - " + this.title;
    this.inst = new mdui.Drawer("#main-drawer");
  },
  beforeCreate() {
    document
      .querySelector("body")
      .setAttribute(
        "class",
        "mdui-bottom-nav-fixed mdui-appbar-with-toolbar mdui-theme-primary-pink mdui-theme-accent-pink mdui-theme-layout-auto mdui-loaded mdui-drawer-body-left"
      );
  },
  methods: {
    isMobile() {
      let flag = navigator.userAgent.match(
        /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
      );
      return flag;
    },
    timeOut(that, callback, time) {
      setTimeout(function () {
        callback(that);
      }, time);
    },
    navto(nav) {
      if (this.isMobile() != null) {
        this.inst.close();
      }
      if (nav != this.pageURL) {
        this.isLoadingPage = true;
      }
      this.timeOut(
        this,
        function (that) {
          try {
            that.$router.push(nav);
          } catch (err) {}
        },
        600
      );
    },
  },
  props: ["title", "subTitle", "isLoading", "pageURL"],
};
</script>



